@import '../../styles/_utils//mixins';
@import '../../styles/_utils/_variables';

.title {
    margin-bottom: 8px;

    font-weight: 800;
    font-size: 32px;
    line-height: 56px;
    letter-spacing: -0.01em;

    color: $grey-primary-clr;

    @include onTablet {
      font-size: 48px;
    }
  }

.modelsCount {
    margin-bottom: 40px;

    font-weight: 600;
    font-size: 14px;
    line-height: 21px;

    color: $grey-secondary-clr;
  }

.navInfo {
  display: flex;
  align-items: center;

  margin-bottom: 40px;
  &__homeLink {
    height: 16px;
    width: 16px;
  }
  &__arrow {
    height: 16px;
    width: 16px;
  }
  &__title {
    font-weight: 600;
    font-size: 12px;

    color: $grey-secondary-clr;
  }

  & > :not(:last-child) {
    margin-right: 8px;
  }
}

.filters {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
}

